<template>
  <div>
    <div class="text-left">
      <!-- 输入筛选区 -->
      <el-row class="serach-box">
        <el-col v-for="item in headerItems" :key="item.label" :span="4">
          <el-col :span="7">{{item.label}}</el-col>
          <el-col :span="16"><el-input v-model="userQuery[item.prop]" clearable placeholder="请输入内容">
            <el-button @click="query" slot="append" icon="el-icon-search"></el-button>
          </el-input></el-col>
        </el-col>
        <el-col :span="4">
          <el-col :span="7">状态</el-col>
          <el-col :span="16">
            <el-select @change="query" v-model="userQuery.hookState">
            <el-option
              v-for="item in statusTypeList"
              :key="item.index"
              :value="item"
            ></el-option>
          </el-select>
          </el-col>
        </el-col>
        <el-col :span="4">
          <el-date-picker
            v-model="dataValue"
            type="datetimerange"
            placeholder="选择日期"
            value-format="yyyy-MM-dd HH:mm:ss"
          >
          </el-date-picker>
        </el-col>
      </el-row>
      <!-- 状态区 -->
      <el-row class="serach-box">
        <el-col :span="4">
          <el-col :span="7">批量更新</el-col>
          <el-col :span="16">
          <el-select v-model="selectStatus">
            <el-option
              v-for="item in statusChangeList"
              :key="item.index"
              :value="item"
            ></el-option>
          </el-select></el-col>
        </el-col>
        <el-col :span="8" class="btn-area" >
        <el-col :span="6"
          ><el-button type="info">更新状态</el-button></el-col
        >
         <el-col :span="6"><el-button type="info">导出</el-button></el-col>
        <el-col :span="6"
          ><el-button @click="toAddHook" type="info">新建</el-button></el-col
        >
        <el-col :span="6"><el-button @click="remove" type="danger">删除</el-button></el-col>
        </el-col>
      </el-row>
      <!-- 下拉筛选区 -->
      <el-row class="serach-box">
        <el-col :span="8" class="status-area">
          <span>已完成:{{ finished }} </span>
          <span>未完成:{{ notFinished }} </span>
          <span>修改中:{{ modified }} </span>
          <span>取消:{{ cancel }}</span></el-col
        >
      </el-row>
      <!-- 表格区 -->
      <el-row>
        <el-table @select="rowclick" border :data="tableData">
          <el-table-column type="selection" width="50"></el-table-column>
          <el-table-column
            type="index"
            label="序号"
            fixed
            width="50"
          ></el-table-column>
          <el-table-column fixed label="订单号" width="100">
            <template slot-scope="scope">
              <el-button type="text" @click="edit(scope)">{{
                scope.row.orderNumber
              }}</el-button>
            </template>
          </el-table-column>
          <el-table-column
            :fixed="item.isFix || false"
            :label="item.label"
            :prop="item.prop"
            :width="item.width"
            v-for="item in tableList"
            :key="item.index"
          >
          </el-table-column>
          <el-table-column label="钩子图片">
            <template slot-scope="scope">
              <el-image
                style="width: 60px; height: 60px; line-height: 60px"
                :src="scope.row.url"
                :preview-src-list="[scope.row.url]"
              >
              </el-image>
            </template>
          </el-table-column>
          <el-table-column
            :fixed="item.isFix || false"
            :label="item.label"
            :prop="item.prop"
            :width="item.width"
            v-for="item in tableList2"
            :key="item.index"
          >
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="80">
            <template slot-scope="scope">
              <el-button @click="file(scope)" type="text" size="small"
                >文档</el-button
              >
              <!-- :disabled="!scope.row.operation" -->
              <el-button
                @click="handleClick(scope.$index)"
                type="text"
                size="small"
                >图稿</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </el-row>
    </div>
  </div>
</template>

<script>
import { GetAllHooks,DeleteHook} from "network/hook.js";
import { GetCommonFormState } from "network/common.js";

import { getAllHooksProcess } from "service/Business/hook.js";
export default {
  data() {
    return {
      search: "",
      tableType: null,
      statusType: null,
      dataValue: null,
      finished: null,
      notFinished: null,
      modified: null,
      cancel: null,
      selectData:[],
      selectStatus: "",
      statusTypeList: [],
      statusChangeList: [],
      tableList: [
        { label: "货号", prop: "productId", width: "150", isFix: "left" },
        { label: "业务员", prop: "salesManName", width: "100" },
        { label: "客户", prop: "customerName", width: "100" },
        { label: "钩子名称", prop: "hookName", width: "150" },
      ],
      headerItems:[{
        label:"订单号",
        prop:"orderId"
      },{
        label:"货号",
        prop:"productId"
      },{
        label:"业务员",
        prop:"salesMan"
      },{
        label:"客户",
        prop:"customer"
      }],
      tableList2: [
        { label: "数量", prop: "number", wdith: "100" },
        { label: "交期", prop: "requiredTime", wdith: "100" },
        { label: "采购方", prop: "purchaser", width: "120" },
        { label: "供应商", prop: "supplier", width: "120" },
        { label: "单价", prop: "price", width: "80" },
        { label: "备注", prop: "comment2", width: "80" },
        {
          label: "下单日期",
          prop: "creationTime",
          width: "150",
          isFix: "right",
        },
        { label: "状态", prop: "hookState", width: "150", isFix: "right" },
      ],
      tableData: [],
      userQuery: {
        orderId: "",
        productId: "",
        salesMan: "",
        customer: "",
        beginTime: "2021-01-03 03:24:38",
        endTime: "2021-01-17 03:24:38",
        hookState: "完成",
        pageSize: 10,
        pageNumber: 1,
      },
    };
  },
  watch: {
    dataValue(val) {
      this.userQuery.beginTime = val[0];
      this.userQuery.endTime = val[1];
      this.query();
    },
  },
  created() {
    this.inital();
  },
  methods: {
    //初始化
    inital() {
      this.getStatusList();
      this.getHookList(this.userQuery);
    },
    //获得状态列表
    getStatusList() {
      GetCommonFormState().then((res) => {
        if (res.success) {
          this.statusTypeList = res.result;
          this.statusChangeList = res.result;
        }
      });
    },
    //获得钩子列表
    getHookList(data) {
      GetAllHooks(data).then((res) => {
        if (res.success) {
          this.$message.success("获得钩子列表成功");
          this.tableData = res.result.hookDto;
          this.totalAmount = res.result.totalAmount;
          this.finished = res.result.finished;
          this.notFinished = res.result.notFinished;
          this.modified = res.result.modified;
          this.cancel = res.result.cancel;
        }
      });
    },
    //筛选查询
    query() {
      this.getHookList(this.userQuery);
    },
    //点击选择框
    rowclick(selection, row) {
      this.selectData = selection;
    },
    //点击订单号链接
    edit(scope) {
      this.$router.push({
        path: "/purchaseHook",
        query: { iid: scope.row.releatedHookGuid },
      });
    },
    //删除
    remove() {
      var data = this.selectData;
      if (data.length == 0) {
        this.$message.error("请先选择项！");
      } else {
        let arr = new Array(data.length);
        data.forEach((item, index) => {
          arr[index] = item.hook_Order_DetailId;
        });
        DeleteHook(arr).then((res) => {
          if (res.success) {
            this.$message.success("删除成功");
          }
        });
      }
    },
    toAddHook() {
      this.$router.push("/purchaseHook");
    },
  },
};
</script>

<style scoped>
.serach-box {
  text-align: left;
  line-height: 40px;
}
.el-row {
  margin-bottom: 10px;
}
.status-area span {
  line-height: 40px;
  margin-right: 20px;
}
</style>